@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    overflow: hidden;
    flex: 0.5;
    .imgContainer {
      width: 700px;
      height: 700px;
      // background-color: crimson;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;

      @include mobile {
        align-items: flex-start;
      }

      img {
        // height: 100%;

        @include mobile {
          height: 50%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;
    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      h1 {
        font-size: 50px;
        margin: 10px 0;

        @include mobile {
          font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }

        span {
          font-size: 30px;
          color: crimson;
        }
        .ityped-cursor {
          animation: blink 1s infinite;
        }
        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 40%;

      img {
        width: 50px;
        animation: arrowblink 2s infinite;
      }
      @keyframes arrowblink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
